<template>
  <div class='guide'>
    <section class="carousel">
      <el-carousel height="580px" :interval="3000" indicator-position="none" arrow="never">
        <el-carousel-item v-for="item in 2" :key="item">
          <img :src="getBanner(item)">
        </el-carousel-item>
      </el-carousel>
      <div class="slogan">
        
      </div>
      <div class="sub-slogan">
        
      </div>
      <div class="btn-bar">
        <el-button round type="plain" class="btn1" @click="toLogin"></el-button>
        <el-button round type="primary" @click="toLogin"></el-button>
      </div>
    </section>
    <section class="feature">
      <div class="title">
        
      </div>
      <div style="border-bottom: 1px solid rgba(7, 17, 27, 0.1); margin-bottom: 50px">
        <div class="sub-title"></div>
        <div class="content">
          <div class="block">
            <div class="image-box"><img src="./check.png"></div>
            <div class="block-title"></div>
            <ul>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
          <div class="block">
            <div class="image-box"><img src="./label.png"/></div>
            <div class="block-title"></div>
            <ul>
              <li>UI</li>
              <li></li>
              <li></li>
            </ul>
          </div>
          <div class="block">
            <div class="image-box">
              <img src="./point.png" width="49" height="49"/>
            </div>
            <div class="block-title"></div>
            <ul>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
        </div>
      </div>
      <div>
        <div class="sub-title"></div>
        <div class="content">
          <div class="block">
            <div class="image-box"><img src="./publish.png" width="49" height="49"></div>
            <div class="block-title"></div>
            <ul>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
          <div class="block">
            <div class="image-box"><img src="./point.png" width="49" height="49"/></div>
            <div class="block-title"></div>
            <ul>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
          <div class="block">
            <div class="image-box">
              <img src="./result.png" width="49" height="49"/>
            </div>
            <div class="block-title"></div>
            <ul>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
        </div>
      </div>
    </section>
    <footer>2018  Crafted by Horizon</footer>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'guide',
    methods: {
      getBanner: function (item) {
        if (item) {
          return '/static/image/banner' + item + '.jpg'
        }
      },
      toLogin: function () {
        this.$router.push('/login')
      }
    }
  }
</script>

<style lang="stylus">
  .guide
    width: 100%
    height: 100%
    .carousel
      position relative
      height 580px
      .layer
        z-index: 10
        position absolute
        left 0
        top 0
        width: 100%
        height 580px
        background-color rgba(255, 255, 255, 0.5)
      .slogan
        z-index: 100
        position absolute
        left 0
        top 200px
        width: 100%
        text-align center
        color #fff
        font-size 50px
        letter-spacing 10px
      .sub-slogan
        z-index: 100
        position absolute
        left 0
        top 280px
        width: 100%
        text-align center
        color #fff
        font-size 26px
      .btn-bar
        z-index: 100
        position absolute
        left 0
        top 400px
        width: 100%
        text-align center
        transform translateX(10px)
        .el-button
          z-index: 100
          line-height 56px
          font-size 18px
          padding 0 25px
          margin-right 10px
          border-radius 30px
        .btn1
          background-color transparent
          color #fff
          border 2px #fff solid

    .feature
      padding 50px 100px
      .title
        margin-bottom 50px
        width 100%
        text-align center
        font-size 36px
        letter-spacing 7px
      .sub-title
        margin-bottom 50px
        width 100%
        text-align center
        font-size 28px
      .content
        display flex
        .block
          flex 1
          padding-bottom 50px
          .image-box
            margin auto
            width 110px
            height 110px
            padding-top 30px
            box-sizing border-box
            border-radius 50%
            background-color #A4DEF9
            text-align center
          .block-title
            margin 30px 0
            width 100%
            text-align center
            font-size 20px
          ul
            text-align center
            opacity 0.7
            li
              margin-bottom 10px
    footer
      width: 100%
      height 50px
      padding 20px 0 0 0
      text-align center
</style>



